import Vue from 'vue'
import Router from 'vue-router'

import SelectPage from "./pages/SelectPage";
import ContentPage from "./pages/ContentPage";

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'SelectPage',
            component: SelectPage,
        },
        {
            path: '/ContentPage',
            name: 'ContentPage',
            component: ContentPage,
        }
    ]
})
