<script>
export default {
  data() {
    return {
      card_index: "",
      card_name: "",
      details: {},
      is_start: false,
    };
  },
  mounted() {
    this.card_index = this.$route.params.card_index;
    this.card_name = this.$route.params.card_name;
    this.details = this.$route.params.card_details;

    let dom = window.document.getElementById("threeContent");
    window.TE.add2dom(dom);

    window.TE.model_show2(this.card_name);

    dom.addEventListener("touchstart", this.touch, false);
    dom.addEventListener("touchmove", this.touch, false);
  },
  activated() {
    this.card_index = this.$route.params.card_index;
    this.card_name = this.$route.params.card_name;
    this.details = this.$route.params.card_details;

    window.TE.model_show2(this.card_name);
  },
  deactivated() {
    //console.log("失活了");
    window.TE.model_hide();
  },
  methods: {
    touch(e) {
      //console.log("@@触碰类型", e.type);
      //if (e.touches.length >= 2) {
      //  alert("@@双指触碰开始", e.touches.length);
      //}

      let distance, clientX_start, clientX_end;

      switch (e.type) {
        case "touchstart":
          clientX_start = e.touches[0].clientX;
          this.clientX_start = clientX_start;
          break;
        case "touchend":
          this.callbackFun();
          break;
        case "touchmove":
          e.preventDefault();

          break;
      }
    },
    //播放动画
    start_animateAciton() {
      return window.TE.start_animateAciton(this.card_name);
    },

    auto_rotateAnimate() {
      return window.TE.auto_rotateAnimate(this.card_name);
    },
  },
};
