<template>
  <div class="centent_box" @touchstart="handelTouchStart" @touchend="handelTouchend">
    <article class="cards_box">
      <div
        class="card"
        v-for="(card, index) in cards_arr"
        :key="index"
        @click="toContentPage"
      >
        <img :src="card.url" alt="" />
      </div>
    </article>
    <div class="now_card_name">{{ now_card_name }}</div>
    <!-- 模型加载进度度条 -->
    <article class="loading_page">
      <!--<div class="logo"></div>-->
      <div class="loading">
        <!--<p id="loading">加载中 (Loading...)</p>-->
        <div class="processContet">
          <div class="process"></div>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
import { threeEngine } from "../js/threeEngine";

export default {
  data() {
    return {
      isLoading: false,
      //左右滑动判断参数
      startTime: 0,
      startX: 0,

      //卡片信息数组
      cards_arr: [
        {
          name: "chuntian__1",
          pic_url: "chuntian",
          url: "./images/chuntian.jpg",
          details: {
            name: "春天",
            type: "烤烟型",
            tar: "10mg",
            alkali: "0.9mg",
            co: "9mg",
            packing: "89mm硬盒包装",
            retail: "300元/条",
          },
        },
        {
          name: "furongwang__2",
          pic_url: "furongwang",
          url: "./images/furongwang.jpg",
          details: {
            name: "芙蓉王",
            type: "",
            tar: "",
            alkali: "",
            co: "",
            packing: "",
            retail: "",
          },
        },
        {
          name: "rongyao__3",
          pic_url: "rongyao",
          url: "./images/rongyao.jpg",
          details: {
            name: "荣耀",
            type: "",
            tar: "",
            alkali: "",
            co: "",
            packing: "",
            retail: "",
          },
        },
        //{
        //  name: "4",
        //  pic_url: "../assets/images/chuntian.png",
        //  details: {
        //    name: "暂无",
        //    type: "",
        //    tar: "",
        //    alkali: "",
        //    co: "",
        //    packing: "",
        //    retail: "",
        //  },
        //},
        //{
        //  name: "5",
        //  pic_url: "",
        //  details: {
        //    name: "暂无",
        //    type: "",
        //    tar: "",
        //    alkali: "",
        //    co: "",
        //    packing: "",
        //    retail: "",
        //  },
        //},
      ],
      now_card_name: "",
    };
  },
  mounted() {
    //初始化3d卡片轮播
    this.wrapper();
    //console.log("selectPage挂载");
  },
  methods: {
    //传参跳转模型内容页面
    toContentPage() {
      window.TE.set_now_showMOdel_name("?");

      this.$router.push({
        name: "ContentPage",
        params: {
          card_index: this.current,
          card_details: this.cards_arr[this.current].details,
          card_name: this.cards_arr[this.current].pic_url,
        },
      });
    },

    //3d卡片轮播_00
    wrapper() {
      this.cards = document.getElementsByClassName("card");
      this.cards_length = this.cards.length;
      this.current = 0;
      this.timer;

      this.frount();
      this.bind();
    },
    //3d卡片轮播_01
    frount() {
      let mlen = Math.floor(this.cards_length / 2);
      let limg, rimg;
      for (let i = 0; i < mlen; i++) {
        limg = this.cards_length + this.current - i - 1;
        rimg = this.current + i + 1;

        if (limg >= this.cards_length) {
          limg -= this.cards_length;
        }
        if (rimg >= this.cards_length) {
          rimg -= this.cards_length;
        }
        this.cards[limg].style.transform =
          `translateX(` +
          -63 * (i + 1) +
          `px) translateZ(` +
          (200 - i * 100) +
          `px) rotateY(30deg)`;
        this.cards[rimg].style.transform =
          `translateX(` +
          63 * (i + 1) +
          `px) translateZ(` +
          (200 - i * 100) +
          `px) rotateY(-30deg)`;
      }
      this.cards[this.current].style.transform = `translateZ(300px)`;

      for (let i = 0; i < this.cards.length; i++) {
        this.cards[i].style.backgroundColor = `rgba(255, 255, 255, 0.92)`;
      }
      this.cards[this.current].style.backgroundColor = `rgba(255, 255, 255)`;
      //console.log("样式", this.cards[this.current].style);

      this.now_card_name = this.cards_arr[this.current].details.name;
    },
    //3d卡片轮播_02
    bind() {
      let that = this;
      for (var i = 0; i < this.cards_length; i++) {
        (function (i) {
          //console.log("@@this", that);
          that.cards[i].onclick = function () {
            that.current = i;
            that.frount();
          };
        })(i);

        //鼠标进入离开卡片后切换
        //this.cards[i].onmouseenter = function () {
        //  clearInterval(that.timer);
        //};
        //this.cards[i].onmouseout = function () {
        //  that.autoplay();
        //};
      }
    },
    //3d卡片轮播_03
    autoplay() {
      let that = this;

      if (that.current >= that.cards_length - 1) {
        that.current = 0;
      } else {
        that.current++;
      }
      that.frount();

      //定时切换
      //this.timer = setInterval(function () {
      //  if (that.current >= that.cards_length - 1) {
      //    that.current = 0;
      //  } else {
      //    that.current++;
      //  }
      //  that.frount();
      //}, 2000);
    },

    //左右滑动判断
    handelTouchStart(e) {
      //console.log("按下", e.changedTouches[0].clientX);
      this.startTime = Date.now();
      this.startX = e.changedTouches[0].clientX;
    },
    handelTouchend(e) {
      //console.log("松开", e.changedTouches[0].clientX);
      let endTime = Date.now();
      let endX = e.changedTouches[0].clientX;
      //let endY = e.changedTouches[0].clinetY;
      if (endTime - this.startTime > 2000) {
        //正常操作不会大于2秒
        //return;
      }
      let direction_value = this.startX - endX;

      //console.log(direction_value > 0 ? "向左滑动" : "向右滑动");
      if (Math.abs(direction_value) > 2 && this.isLoading == false) {
        //划定距离大于7px才算滑动
        if (direction_value > 0) {
          if (this.current != this.cards.length - 1) {
            this.current++;
            this.frount();
          } else {
            this.current = 0;
            this.frount();
          }
        } else {
          //console.log("距离", Math.abs(direction_value));
          if (this.current != 0) {
            this.current--;
            this.frount();
          } else {
            this.current = this.cards.length - 1;
            this.frount();
          }
        }
      }
    },
  },
};
</script>

<style scoped lang="less">
.loading_page {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255);
  z-index: 999999;

  .logo {
    width: 10rem;
    height: 14rem;

    position: absolute;
    top: 10rem;

    margin-left: 50%;
    transform: translateX(-50%);

    background: url(./../../public/images/logo_img.png) no-repeat center;
    background-size: 100%;
  }
}
.loading {
  position: fixed;
  //bottom: 5rem;
  bottom: 6.5rem;
  z-index: 999999;
  width: 100%;
  //height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  //background: rgba(255, 255, 255, 0.6);
  color: gray;
  /* background: url(../img/bg.jpg)no-repeat center;
    background-size: 100% 100%; */
}
.processContet {
  background-color: #ffffff;
  border: 1px solid #000000;
  width: 50%;
  //width: 8rem;
  border-radius: 16px;
  margin: 0.2rem auto;
  display: flex;
  height: 0.2667rem;
  //height: 0.4667rem;
  overflow: hidden;
  //padding-top: -1px;
  //padding-: -1px;
}
.process {
  width: 0px;
  left: 0;
  height: 0.4667rem;
  background-color: #000000;
}
#loading {
  color: #aeaeae;
  font-size: 0.3rem;
  width: 100%;
  text-align: center;
}

.now_card_name {
  position: absolute;
  bottom: 8rem;
  margin-left: 50%;
  transform: translateX(-50%);
  color: #f9dc77;
  font-weight: bold;
}
.centent_box {
  width: 100%;
  height: 100%;
  background-color: #0e1c1e;

  background: url(./../../public/images/card_background.jpg) no-repeat center;
  //background-size: 100%;

  color: white;
  position: relative;

  .cards_box {
    width: 100%;
    height: 30rem;
    //background-color: rebeccapurple;
    display: none;

    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;

    perspective: 1100px;
    //perspective: 800px;//默认, (视角,卡片距离屏幕的距离
    transform-style: preserve-3d;
  }

  .card {
    width: 13rem;
    height: 18rem;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    //background: url(./../../public/images/card_background.jpg) no-repeat center;
    //background-size: 100%;
    color: #000000;
    box-shadow: 5px 5px 5px #0000008f;
    //border: #000000 1px solid;

    position: absolute;
    top: 0;
    bottom: 5rem;
    left: 0;
    right: 0;
    margin: auto;

    //卡片变化速度
    transition: transform 0.2s ease-in-out;

    //正片叠底div标签
    //background-blend-mode: multiply;

    img {
      width: 12rem;
      height: 16rem;
      //background-color: rebeccapurple;

      //正片叠底img标签
      //mix-blend-mode: multiply;
      //mix-blend-mode: overlay; //叠加
      //mix-blend-mode: lighten; //变亮

      border-radius: 10px;

      position: absolute;
      top: 1.5rem;
      //bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }
}
</style>
