import * as THREE from 'three'
import { Clock, LoadingManager } from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader";
import $ from 'jquery'

export class threeEngine {
    card_index

    dom
    scene
    camera
    renderer

    ambientLight
    directionalLight

    model_list = []

    controls = null

    now_showMOdel_name = ""

    mixer_chuntian = null
    mixer_furongwang = null
    mixer_rongyao = null

    action_chuntian = null
    action_furongwang = null
    action_rongyao = null

    environmentMap = null

    is_start_chuntian = false
    is_start_furongwang = false
    is_start_rongyao = false

    is_auto_chuntian = true
    is_auto_furongwang = true
    is_auto_rongyao = true
    now_name = ""

    constructor() {

        this.scene_init()
        this.light_init()
        this.controls_init()

        this.loadModel()

        //窗口大小发生变化
        this.viewResize()

        //this.help_init()

    }

    //初始化场景
    scene_init() {

        this.scene = new THREE.Scene()

        this.camera = new THREE.PerspectiveCamera((45, window.innerWidth / window.innerHeight, 0.1, 10000))
        // 修改相机的镜头比例
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.position.set(0, 4, 26)
        this.camera.setFocalLength = 12
        this.camera.updateProjectionMatrix()

        //相机方向转回来
        //this.camera.up.x = 0;
        //this.camera.up.y = -1;
        //this.camera.up.z = 0;

        this.renderer = new THREE.WebGLRenderer({
            alpha: true,
            antialias: true,
            sortObjects: true,
            logarithmicDepthBuffer: true, //去掉模型闪烁
        })
        //this.renderer.setSize(innerWidth, innerHeight);
        this.renderer.setClearColor("#f0eded", 0)
        this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
        //this.renderer.physicallCorrectLight=true
        //this.dom.appendChild(this.renderer.domElement);

        //每帧动画函数
        let clock = new THREE.Clock();
        let previous_Time = 0
        let animate = () => {
            const elapsed_TIme = clock.getElapsedTime()
            const delta_Time = elapsed_TIme - previous_Time
            previous_Time = elapsed_TIme

            if (this.controls != null) {
                this.controls.update();

            }

            if (this.mixer_chuntian != null) {
                this.mixer_chuntian.update(delta_Time * 6)
            }
            if (this.mixer_furongwang != null) {
                this.mixer_furongwang.update(delta_Time * 6)
            }
            if (this.mixer_rongyao != null) {
                this.mixer_rongyao.update(delta_Time * 6)
            }



            //自动旋转
            if (this.is_auto_chuntian) {
                this.model_list.forEach(item => {
                    if (this.now_name == "chuntian") {
                        //if (this.is_auto_chuntian) {
                            item.rotateY(0.006)
                        //}
                        
                    }
                })

                //this.model_list[0].rotateY(0.006)

            }
            if (this.is_auto_furongwang) {
                this.model_list.forEach(item => {
                    if (this.now_name == "furongwang") {
                        //console.log("对应",item);	
                        //if (this.is_auto_furongwang) {
                            item.rotateY(0.006)
                        //}
                        
                    }
                })
                //this.model_list[2].rotateY(0.006)

            } 
            if (this.is_auto_rongyao) {
                this.model_list.forEach(item => {
                    if (this.now_name == "rongyao") {
                        //console.log("对应",item);	
                        //if (this.is_auto_rongyao) {
                            item.rotateY(0.006)
                        //}
                        
                    }
                })
                

            }

            //console.log('@@缩放',this.camera.zoom);
            if (this.controls != null) {
                //console.log("缩放", this.controls);

            }



            this.renderer.render(this.scene, this.camera);

            requestAnimationFrame(animate);
        }
        animate()

    }
    //自动旋转
    auto_rotateAnimate(name) {
        //this.now_name = name
        if (name == "chuntian") {
            this.is_auto_chuntian = !this.is_auto_chuntian
        } else if (name == "furongwang") {
            this.is_auto_furongwang = !this.is_auto_furongwang
        } else if (name == "rongyao") {
            this.is_auto_rongyao = !this.is_auto_rongyao
        }
    }
    //播放动作
    start_animateAciton(name) {
        if (name == "chuntian") {
            //this.action_chuntian.play()
            //this.action_chuntian.paused = true;
            //console.log("动作");
            //this.action_chuntian.repetitions = 1
            if (!this.is_start_chuntian) {
                //this.action_chuntian.play()
                this.action_chuntian.paused = false
            } else {
                //console.log("暂停");
                this.action_chuntian.paused = true
            }
            this.is_start_chuntian = !this.is_start_chuntian
            //console.log("动作", this.action_chuntian);
        } else if (name == "furongwang") {
            //this.action_furongwang.repetitions = 1
            //this.action_furongwang.play()
            if (!this.is_start_furongwang) {
                this.action_furongwang.play()
                this.action_furongwang.paused = false
            } else {
                //console.log("暂停");
                this.action_furongwang.paused = true
            }
            this.is_start_furongwang = !this.is_start_furongwang
        } else if (name == "rongyao") {
            //this.action_rongyao.repetitions = 1
            //this.action_rongyao.play()
            if (!this.is_start_rongyao) {
                //this.action_rongyao.play()
                this.action_rongyao.paused = false
            } else {
                //console.log("暂停");
                this.action_rongyao.paused = true
            }
            this.is_start_rongyao = !this.is_start_rongyao
        }
    }
    //添加renderer的domElement到dom
    add2dom(dom) {
        //console.log(dom.offsetX,dom.offsetY);
        this.dom = dom
        this.dom.appendChild(this.renderer.domElement);
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.controls_init()

        //console.log(this.scene);

        console.log("dom添加完毕");
    }
    //初始化灯光
    light_init() {
        //环境光
        this.ambientLight = new THREE.AmbientLight(0x404040, 3.5)
        //this.ambientLight = new THREE.AmbientLight("#ffffff", 10);
        this.scene.add(this.ambientLight)

        //平行光
        this.directionalLight = new THREE.DirectionalLight(0xffffff, .6)
        this.directionalLight.position.set(15, 15, -15)
        this.scene.add(this.directionalLight)
        //平行光2
        this.directionalLight2 = new THREE.DirectionalLight(0xffffff, .6)
        this.directionalLight2.position.set(-15, -15, 15)
        this.scene.add(this.directionalLight2)
    }
    //初始化控制器
    controls_init() {
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        //平移
        this.controls.enablePan = false
        //缩放 
        this.controls.enableZoom = false

        //启用阻尼(在动画处更新控制器)
        this.controls.enableDamping = true

        //能将相机向外移动多少
        this.controls.maxDistance = 25

        //能垂直旋转的角度的上限    
        this.controls.maxPolarAngle = Math.PI / 1.8
        //能垂直旋转的角度的下限
        this.controls.minPolarAngle = Math.PI / 4.2

        //只在正交相机有用
        //this.controls.maxZoom = -3
        //this.controls.minZoom = 5

        this.controls.addEventListener("change", () => {
            //console.log('控制器', this.scene.children[3])
        });

        this.controls.target = new THREE.Vector3(0, 3, 0)
        this.controls.update()//更新轨道控制器(修改了轨道环绕中心后)

    }
    //摄像机放大
    camera1_2near() {
        if (this.camera.zoom < 3) {
            this.camera.zoom += 0.1
            this.camera.updateProjectionMatrix()
        }
    }
    //摄像机缩小
    camera1_2far() {
        if (this.camera.zoom > 1.1) {
            this.camera.zoom -= 0.1
            this.camera.updateProjectionMatrix()
        }
    }
    updateAllMaterials() {
        this.scene.traverse((child) => {
            if (child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial) {
                // child.material.envMap = environmentMap
                child.material.envMapIntensity = 2//环境贴图强度
                child.material.needsUpdate = true
                //child.castShadow = true
                //child.receiveShadow = true
            }
        })
    }
    //模型&材质 加载
    loadModel() {
        //进度管理器
        let loadingManager = new LoadingManager()
        loadingManager.onStart = () => { }
        loadingManager.onLoaded = () => { }
        loadingManager.onProgress = (xhr) => {
            //console.log('@@@',xhr);
            //进度监听事件
            //let processContet_width = $(".processContet").css("width").replace("px", "") * (xhr.loaded / xhr.total)
            //document.getElementById('loading').innerText = "加载" + parseInt(xhr.loaded / xhr.total * 100) + '%';
            //$(".process").css("width", processContet_width)
            //// console.log( "加载" + parseInt(xhr.loaded / xhr.total * 100 ) + '%');
            //if ((xhr.loaded / xhr.total * 100) >= 100) {

            //    document.getElementById('loading').innerText = '加载完请稍后...';
            //    setTimeout(function () {
            //        $(".loading").fadeOut();
            //    }, 1000);
            //}
        }
        loadingManager.onError = () => { }

        //环境贴图
        const cubeTextureLoader = new THREE.CubeTextureLoader()

        this.environmentMap = cubeTextureLoader.load([
            './environmentMaps/px.jpg',
            './environmentMaps/nx.jpg',
            './environmentMaps/py.jpg',
            './environmentMaps/ny.jpg',
            './environmentMaps/pz.jpg',
            './environmentMaps/nz.jpg'

        ])
        this.environmentMap.encoding = THREE.sRGBEncoding
        this.scene.environment = this.environmentMap


        //纹理加载器
        let textureLoader = new THREE.TextureLoader(loadingManager)

        //春天材质 lambert_1
        let chuntian_lambert1 = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/chuntian/chuntian_lambert1_BaseColor.png'),
            aoMap: textureLoader.load('./model/chuntian/chuntian_lambert1_Ao.png'),
            normalMap: textureLoader.load('./model/chuntian/chuntian_lambert1_Normal.png'),
            roughnessMap: textureLoader.load('./model/chuntian/chuntian_lambert1_Roughness.png'),
            metalnessMap: textureLoader.load('./model/chuntian/chuntian_lambert1_Metallic.png')
        })
        chuntian_lambert1.aoMapIntensity = .3
        //春天材质 lambert_3
        let chuntian_lambert3 = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/chuntian/chuntian_lambert3_BaseColor.png'),
            aoMap: textureLoader.load('./model/chuntian/chuntian_lambert3_Ao.png'),
            normalMap: textureLoader.load('./model/chuntian/chuntian_lambert3_Normal.png'),
            roughnessMap: textureLoader.load('./model/chuntian/chuntian_lambert3_Roughness.png'),
            metalnessMap: textureLoader.load('./model/chuntian/chuntian_lambert3_Metallic.png')
        })
        chuntian_lambert3.aoMapIntensity = .5
        //春天材质 lambert4
        let chuntian_lambert4 = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/chuntian/chuntian_lambert4_BaseColor.png'),
            aoMap: textureLoader.load('./model/chuntian/chuntian_lambert4_Ao.png'),
            normalMap: textureLoader.load('./model/chuntian/chuntian_lambert4_Normal.png'),
            roughnessMap: textureLoader.load('./model/chuntian/chuntian_lambert4_Roughness.png'),
            metalnessMap: textureLoader.load('./model/chuntian/chuntian_lambert4_Metallic.png')
        })
        chuntian_lambert4.aoMapIntensity = .3

        //芙蓉王材质 initialShadingGroup
        let furongwang_initialShadingGroup = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/furongwang/furongwang_initialShadingGroup_BaseColor.png'),
            aoMap: textureLoader.load('./model/furongwang/furongwang_initialShadingGroup_Ao.png'),
            normalMap: textureLoader.load('./model/furongwang/furongwang_initialShadingGroup_Normal.png'),
            roughnessMap: textureLoader.load('./model/furongwang/furongwang_initialShadingGroup_Roughness.png'),
            metalnessMap: textureLoader.load('./model/furongwang/furongwang_initialShadingGroup_Metallic.png')
        })
        furongwang_initialShadingGroup.aoMapIntensity = .4
        //芙蓉王材质 lambert2SG
        let furongwang_lambert2SG = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/furongwang/furongwang_lambert2SG_BaseColor.png'),
            aoMap: textureLoader.load('./model/furongwang/furongwang_lambert2SG_Ao.png'),
            normalMap: textureLoader.load('./model/furongwang/furongwang_lambert2SG_Normal.png'),
            roughnessMap: textureLoader.load('./model/furongwang/furongwang_lambert2SG_Roughness.png'),
            metalnessMap: textureLoader.load('./model/furongwang/furongwang_lambert2SG_Metallic.png')
        })
        furongwang_lambert2SG.aoMapIntensity = .4
        //芙蓉王材质 lambert3SG
        let furongwang_lambert3SG = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/furongwang/furongwang_lambert3SG_BaseColor.png'),
            aoMap: textureLoader.load('./model/furongwang/furongwang_lambert3SG_Ao.png'),
            normalMap: textureLoader.load('./model/furongwang/furongwang_lambert3SG_Normal.png'),
            roughnessMap: textureLoader.load('./model/furongwang/furongwang_lambert3SG_Roughness.png'),
            metalnessMap: textureLoader.load('./model/furongwang/furongwang_lambert3SG_Metallic.png')
        })
        furongwang_lambert3SG.aoMapIntensity = .5

        //荣耀材质 lambert4SG
        let rongyao_lambert4SG = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/rongyao/rongyao_lambert4SG_BaseColor.png'),
            aoMap: textureLoader.load('./model/rongyao/rongyao_lambert4SG_Ao.png'),
            normalMap: textureLoader.load('./model/rongyao/rongyao_lambert4SG_Normal.png'),
            roughnessMap: textureLoader.load('./model/rongyao/rongyao_lambert4SG_Roughness.png'),
            metalnessMap: textureLoader.load('./model/rongyao/rongyao_lambert4SG_Metallic.png')
        })
        rongyao_lambert4SG.aoMapIntensity = .3
        //荣耀材质 lambert5SG
        let rongyao_lambert5SG = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/rongyao/rongyao_lambert5SG_BaseColor.png'),
            aoMap: textureLoader.load('./model/rongyao/rongyao_lambert5SG_Ao.png'),
            normalMap: textureLoader.load('./model/rongyao/rongyao_lambert5SG_Normal.png'),
            roughnessMap: textureLoader.load('./model/rongyao/rongyao_lambert5SG_Roughness.png'),
            metalnessMap: textureLoader.load('./model/rongyao/rongyao_lambert5SG_Metallic.png')
        })
        rongyao_lambert5SG.aoMapIntensity = .3
        //荣耀材质 lambert6SG
        let rongyao_lambert6SG = new THREE.MeshStandardMaterial({
            map: textureLoader.load('./model/rongyao/rongyao_lambert6SG_BaseColor.png'),
            aoMap: textureLoader.load('./model/rongyao/rongyao_lambert6SG_Ao.png'),
            normalMap: textureLoader.load('./model/rongyao/rongyao_lambert6SG_Normal.png'),
            roughnessMap: textureLoader.load('./model/rongyao/rongyao_lambert6SG_Roughness.png'),
            metalnessMap: textureLoader.load('./model/rongyao/rongyao_lambert6SG_Metallic.png')
        })
        rongyao_lambert6SG.aoMapIntensity = .5


        //模型加载器
        let loader = new FBXLoader()

        //let box1 = new THREE.Mesh(
        //    new THREE.BoxBufferGeometry(.1, .1, .1),
        //    new THREE.MeshStandardMaterial({
        //        color: '#ff0400'
        //    })
        //)
        //this.scene.add(box1)

        //春天模型
        loader.load('./model/chuntian/chuntian.fbx', (fbx) => {
            this.scene.add(fbx)
            fbx.visible = false
            this.model_list.push(fbx)
            fbx.name = "chuntian"

            //fbx.scale.set(10, 10, 10)
            fbx.position.set(.6, 1, 0)
            //设置轨道围绕的中心
            this.controls.target = new THREE.Vector3(0, 0, 0)
            this.controls.update()//更新轨道控制器(修改了轨道环绕中心后)
            fbx.traverse(function (child) {
                //console.log("模型单个", child);
                if (child.isMesh && (child.name == "hezi" || child.name == "gaizi")) {
                    child.material = chuntian_lambert1
                } else if (child.isMesh && child.name == "hezi_out001Mesh") {
                    child.material = chuntian_lambert4
                } else if (child.isMesh) {
                    child.material = chuntian_lambert3
                }
            })

            //console.log("春天模型载入完毕", fbx);


            this.mixer_chuntian = new THREE.AnimationMixer(fbx)
            this.action_chuntian = this.mixer_chuntian.clipAction(fbx.animations[0])
            this.action_chuntian.play()
            this.action_chuntian.paused = true
            //console.log("动作春天", this.action_chuntian);

            //this.action_chuntian.play()

            //console.log("@@@", this.model_list[0].children[22]);

        })
        //芙蓉王模型
        loader.load('./model/furongwang/furongwang.fbx', (fbx) => {
            this.scene.add(fbx)
            fbx.visible = false
            this.model_list.push(fbx)
            fbx.name = "furongwang"

            fbx.scale.set(.75, .75, .75)
            fbx.position.set(0, 3, 0)

            fbx.traverse(function (child) {
                //console.log("模型单个", child);
                if (child.isMesh && (child.name == "furongwangDown" || child.name == "furongwangUP" || child.name == "pasted__furongwangUP" || child.name == "pasted__furongwangDown")) {
                    child.material = furongwang_initialShadingGroup
                } else if (child.isMesh && (child.name == "furongwangInner" || child.name == "pasted__furongwangInner")) {
                    child.material = furongwang_lambert2SG
                } else if (child.isMesh) {
                    child.material = furongwang_lambert3SG
                }
            })
            //console.log("芙蓉王模型载入完毕", fbx);

            this.mixer_furongwang = new THREE.AnimationMixer(fbx)
            this.action_furongwang = this.mixer_furongwang.clipAction(fbx.animations[0])
            this.action_furongwang.play()
            this.action_furongwang.paused = true
            //console.log("动作芙蓉王", this.action_furongwang);

            //更新全场景环境贴图
            this.updateAllMaterials()

            //console.log('@@@',this.model_list);

        },
            (xhr) => {
                let processContet_width = $(".processContet").css("width").replace("px", "") * (xhr.loaded / xhr.total)
                //document.getElementById('loading').innerText = "加载" + parseInt(xhr.loaded / xhr.total * 100) + '%';
                $(".process").css("width", processContet_width)
                // console.log( "加载" + parseInt(xhr.loaded / xhr.total * 100 ) + '%');
                if ((xhr.loaded / xhr.total * 100) >= 100) {

                    //document.getElementById('loading').innerText = '加载完请稍后...';
                    setTimeout(function () {
                        $(".loading").fadeOut();
                        //$(".loading_page").fadeOut();
                        $(".loading_page").css("display","none");
                        $(".cards_box").css("display","block");
                    }, 1000);
                }
            })
        //荣耀模型
        loader.load('./model/rongyao/rongyao.fbx', (fbx) => {
            this.scene.add(fbx)
            fbx.visible = false
            this.model_list.push(fbx)
            fbx.name = "rongyao"

            fbx.scale.set(.75, .75, .75)
            fbx.position.set(0, 3, 0)

            fbx.traverse(function (child) {
                //console.log("模型单个?", child);
                if (child.isMesh && (child.name == "furongwangDown" || child.name == "furongwangUP")) {
                    child.material = rongyao_lambert4SG
                } else if (child.isMesh && child.name == "furongwangInner") {
                    child.material = rongyao_lambert5SG
                } else if (child.isMesh) {
                    child.material = rongyao_lambert6SG
                }
            })
            //console.log("荣耀模型载入完毕", fbx);

            this.mixer_rongyao = new THREE.AnimationMixer(fbx)
            this.action_rongyao = this.mixer_rongyao.clipAction(fbx.animations[0])
            this.action_rongyao.play()
            this.action_rongyao.paused = true

            //console.log('@@@',this.model_list);

        })




    }

    set_now_showMOdel_name(name) {
        this.now_showMOdel_name = name
    }
    re0_now_showMOdel_name(name) {
        this.now_showMOdel_name = ""
    }
    //模型显示
    model_show(index) {
        console.log("烟的index", index);
        console.log('模型列表', this.model_list);
        console.log('当前模型', this.model_list[index]);
        this.model_list[index].visible = true
    }
    //模型显示
    model_show2(name) {
        this.now_name = name
        this.model_list.forEach(item => {
            if (item.name == name) {
                item.visible = true
            }
        })
        //this.model_list[].visible =    true
    }
    model_hide() {
        this.model_list.forEach(item => {
            item.visible = false
            console.log(item.visible);
        })
    }
    //窗口变化
    viewResize() {
        window.addEventListener("resize", () => {
            //修改画布的大小
            this.renderer.setSize(window.innerWidth, window.innerHeight)
            // 修改相机的镜头比例
            this.camera.aspect = window.innerWidth / window.innerHeight
            // 修改相机的属性后更新相机矩阵
            this.camera.updateProjectionMatrix()
            //当应用在切换屏幕时更新像素比
            this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
        });

    }
    //初始化辅助线
    help_init() {
        const axesHelper = new THREE.AxesHelper(100);
        this.scene.add(axesHelper);
    }

}